<template>
  <app-container>
    <app-header title="查看公告" />
    <div class="content-container">
      <div v-if="announcementData" class="block">
        <p class="title">{{ announcementData.subject }}</p>
        <p class="sub">
          {{ handleTime(announcementData.published_on)
          }}<span class="publisher">{{ announcementData.publisher }}</span>
        </p>
        <van-divider />
        <div v-html="announcementData.content" class="content-area"></div>
      </div>
    </div>
  </app-container>
</template>

<script>
import AnnouncementService from '@services/announcement-service';
export default {
  name: 'AnnouncementDetail',
  data() {
    return {
      announcementData: null,
    };
  },
  activated() {
    this.announcementData = null;
    this.getDetail();
  },
  methods: {
    handleTime(time) {
      const result = time.substring(0, time.length - 3);
      return result;
    },
    getDetail() {
      return AnnouncementService.getAnnouncementDetail({
        announcement_id: this.$route.params.id,
      }).then(res => {
        this.announcementData = res;
      });
    },
  },
};
</script>

