import * as announcementApi from '@src/api/announcement';
import Network from '@yl-app-jssdk/network';
export default {
  getAnnouncementList(data) {
    return Network.getHasNetwork().then(hasNetwork => {
      if (!hasNetwork) {
        return Promise.reject('您的当前网络不可用，请检查您的网络设置');
      }
      return announcementApi.getAnnouncementList(data).then(res => {
        const { data, recordCount } = res;
        return {
          announcementData: data,
          recordCount,
        };
      });
    });
  },

  getAnnouncementDetail(data) {
    return Network.getHasNetwork(data).then(hasNetwork => {
      if (!hasNetwork) {
        return Promise.reject('您的当前网络不可用，请检查您的网络设置');
      }
      return announcementApi.getAnnouncementDetail(data).then(res => {
        return res;
      });
    });
  },

  updateAnnouncementStatus(data) {
    return Network.getHasNetwork(data).then(hasNetwork => {
      if (!hasNetwork) {
        return Promise.reject('您的当前网络不可用，请检查您的网络设置');
      }
      return announcementApi.updateAnnouncementStatus(data).then(res => {
        return res;
      });
    });
  },
};
