import ajax from '@src/common/ajax';
import { getUserContext } from '@yl-app-jssdk/context';

const domain = window.GLOBAL_ENV.SSO_CENTER_DEFAULT_API;
let userId = getUserContext().userId;
let receiveId;

// 通过登录用户openId获取用户id
export const getUseridByOpenid = () => {
  const data = getUserContext();
  // 已经获取过用户id并且当前登录的用户和上一次请求用户相同时，直接返回用户id(区分切换登录账号的情况)
  if (receiveId && data.userId === userId) {
    return Promise.resolve(receiveId);
  }
  userId = data.userId;
  const tenantCode = data.tenantCode;
  return ajax({
    method: 'get',
    url: `${domain}/m/${tenantCode}/PermissionApply/permission-init/get-userid-by-openid`,
    data: {
      tenantCode,
      openId: data.userId,
    },
  })
    .then(res => {
      if (res.errcode) {
        return Promise.reject(res.message);
      }
      receiveId = res.data;
      return res.data;
    })
    .catch(err => {
      throw new Error(err);
    });
};

/**
 * 获取公告列表
 * @param {user_id} 用户id
 * @param {tenant_code} 租户code
 * @param {pageIndex} 当前页码 ，默认1
 * @param {pageSize} 每页记录数 默认 10
 */
export const getAnnouncementList = data => {
  const tenantCode = getUserContext().tenantCode;
  return getUseridByOpenid().then(res => {
    return getList(res);
  });
  function getList(receiveId) {
    return ajax({
      method: 'POST',
      url: `${domain}/m/Organization/api2/announcement-list`,
      data: Object.assign(
        {},
        {
          user_id: receiveId,
          tenant_code: tenantCode,
        },
        data
      ),
    })
      .then(res => {
        if (res.errcode) {
          return Promise.reject(res.errMsg);
        }
        const { list, count } = res.data;
        return { data: list || [], recordCount: count || 0 };
      })
      .catch(() => {
        // throw new Error(err);
        return { data: [], recordCount: 0 };
      });
  }
};

/**
 * 获取公告详情
 * @param {user_id} 用户id
 * @param {tenant_code} 租户code
 * @param {announcement_id} string-公告ID
 */
export const getAnnouncementDetail = data => {
  const tenantCode = getUserContext().tenantCode;
  return ajax({
    method: 'POST',
    url: `${domain}/m/Organization/api2/announcement-info`,
    data: {
      user_id: receiveId,
      tenant_code: tenantCode,
      announcement_id: data.announcement_id,
    },
  })
    .then(res => {
      if (res.errcode) {
        return Promise.reject(res.errMsg);
      }
      return res.data;
    })
    .catch(err => {
      throw new Error(err);
    });
};

/**
 * 更新公告状态
 * @param {user_id} 用户id
 * @param {tenant_code} 租户code
 * @param {announcement_ids} array - 公告id数组
 */
export const updateAnnouncementStatus = data => {
  const tenantCode = getUserContext().tenantCode;
  return getUseridByOpenid().then(res => {
    return updateStatus(res);
  });
  function updateStatus(receiveId) {
    return ajax({
      method: 'POST',
      url: `${domain}/m/Organization/api2/announcement-set-status`,
      data: Object.assign(
        {},
        {
          user_id: receiveId,
          tenant_code: tenantCode,
        },
        data
      ),
    })
      .then(res => {
        if (res.errcode) {
          return Promise.reject(res.errMsg);
        }
        return res;
      })
      .catch(err => {
        throw new Error(err);
      });
  }
};
